eft:50%;display:block;border:1px solid #d41616;background-color:#d41616;width:12px;cursor:pointer}.order-item .btns{display:flex}.order-item .btns div{font-size:16px;border-radius:2px;width:15px;text-align:center;cursor:pointer;margin:0 0 0 10px}@media only screen and (min-width: 995.4px){.menu-list{width:70%}.menu-list>:nth-child(2){margin:10px 0;padding:0 20px;height:auto;height:initial;max-height:calc(100% - 75px - 30px);display:inline-flex;flex-wrap:wrap}.item{width:49%}.order-list{margin:0 0 0 0.5%;width:30%;height:100%;opacity:1;position:static;position:initial;top:auto;top:initial;right:auto;right:initial}.list>:nth-child(1){display:flex}.shop-icon{display:none}.list-icon{display:none}}

